.menu-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(45, 232, 187, 0.7);
  background: linear-gradient(
    90deg,
    rgba(45, 232, 187, 0.7) 0%,
    rgba(45, 160, 187, 0.7) 57%
  );
}

.title {
  font-family: "Montserrat";
  text-align: center;
}

.menu-wrapper {
  background-image: url("../../Assets/bg_pattern.gif");
  background-size: auto;
  background-repeat: repeat;
  background-attachment: fixed;
}

.menu-content {
  background-color: #f8f8f8;
  width: 700px;
  display: inline-block;
  margin: auto;
  line-break: auto;
  word-break: break-all;
  padding: 16px;
}

.name-container-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.name-container {
  background-color: white;
  border-radius: 40px;
  border: 1px solid #dddddd;
}

.insert-name-input {
  height: 50px;
  background: transparent;
  margin-left: 16px;
}

.play-button {
  height: 50px;
  width: 50px;
}
